<template>
    <div class="row">
        <div class="col-2"></div>
        <div class="col-8">

            <q-input type="text" input-class="text-h6" borderless color="secondary"
                v-model="state.title" placeholder="강의명을 입력하세요" style="width:85%" />
            
            <br />
            <video autoplay controls :src="state.source" width="250">
                <!-- <source :src="state.source" type="video/webm">
                    <source :src="state.source" type="video/mp4"> -->
            </video>

            <br />
            <input type="file" @change="handleVideo($event)" />
    
            <br />
            <br />
            <editor v-model="content" style="width:100%;" />
            <div class="content">
                
            </div>
    
            <div align="right">
                <q-btn style="margin:10px;" color="secondary" @click="handleInsertLecture" label="등록" />
            </div>
    
        </div>
        <div class="col-2"></div>

    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute } from 'vue-router'
import Editor from '@/components/editor/GeneralEditor.vue'
import axios from 'axios'
import router from '@/routes'
export default {
    components : {
        Editor,
    },
    data() {
        return {
            content : ''
        }
    },
    methods : {
        handleInsertLecture : async function() {
            console.log(this.content);
            const url = `/COCO/api/lecture/insert.json`;
            const headers = {"Content-Type" : "multipart/form-data"};
            const body = new FormData();
            body.append("title", this.state.title);
            body.append("content", this.content);
            body.append("free", 1);
            body.append("chapterno", this.state.chaptercode);
            body.append("file", this.state.file);

            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200) {
                router.go(-1);
            }
        }
    },
    setup () {
        const route = useRoute();
        const state = reactive({
            chaptercode : route.query.no,
            title : '',
            file : '',
            source : ''
        })

        const handleVideo = (e) => {
            console.log(e);
            if(e.target.files.length > 0) {
                state.file = e.target.files[0];
                state.source = URL.createObjectURL(e.target.files[0]);
            }
            else {
                state.file = '';
                state.source = '';
            }
        }

        return {state, handleVideo}
    }
}
</script>

