<template>
    <div class="container">
        <q-card-section>
            <div class="text-h6 text-secondary">학력</div>
        </q-card-section>
        <div v-if="!state.eduList.length > 0" style="padding:15px" >
            <div class="text-caption text-grey">등록된 학력이 없습니다.</div>
            <div class="text-caption text-grey">최대 3개까지 추가할 수 있습니다.</div>
        </div>
        <div>
                <q-card flat>
                    <q-item>
                        <q-item-section class="vfor-box">
                            <div v-for="tmp of state.eduList" :key="tmp" class="row" style="padding: 15px;">
                                <q-card class="row">

                                    <q-card-section class="row">
                                        <div hidden>{{tmp.educationno}}</div>
                                        <div style="width:160px;">{{tmp.name}}</div>
                                        <div style="width:160px;">{{tmp.department}}</div>
                                        <div style="width:60px;">{{tmp.status}}</div>
                                    </q-card-section>

                                    <q-separator vertical />

                                    <q-card-actions>
                                        <div><q-btn color="negative" flat dense @click="handleDeleteEdu(tmp.educationno)" label="삭제" /></div>
                                    </q-card-actions>

                                </q-card> 
                            </div>
                        </q-item-section>
                    </q-item>
                </q-card>
            </div>
            <q-card-actions>
                <q-btn color="grey" flat dense label="학력 추가" :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'" @click="expanded = !expanded" />
            </q-card-actions>

            <q-slide-transition>
                <div v-show="expanded">
                    <div class="q-pa-md row">
                        <q-input color="secondary" style="width:160px;" dense v-model="state.education.name" class="input-item" autofocus label="학교/학원" />
                        <q-input color="secondary" style="width:160px;" dense v-model="state.education.department" class="input-item" label="학과" />
                        <q-select color="secondary" style="width:60px;" options-dense dense v-model="state.status" :options="options" class="input-item" label="상태" />
                        <q-btn flat dense color="primary" label="추가" @click="handleInsert" />
                    </div>
                </div>
            </q-slide-transition>
        <hr />
    </div>

</template>

<script>import { reactive, ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
export default {
    setup() {
        const state = reactive({
            name : '',
            department : '',
            status : '',
            eduBtn : true,
            edu : false,
            education : [],
            eduList : [],
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData();
        });

        // 버튼 추가
        const handleBtn = () => {
            state.eduBtn = false;
            state.edu = true;
        };

        // 버튼 취소
        const handlePrev = () => {
            state.eduBtn = true;
            state.edu = false;
        };

        // 학력 전체 조회
        const handleData = async() => {
            const url = `/COCO/api/teacher/selectListeducation.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.eduList = data.education;
            }
        };

        // 학력 등록
        const handleInsert = async() => {
            const url = `/COCO/api/teacher/joineducation.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                name : state.education.name,
                department : state.education.department,
                status : state.education.status
            }
            
            const { data } = await axios.post(url, body, {headers});
            console.log(data)
            if(data.status === 200) {
                state.edu = false;
                state.eduBtn = true
                handleData();
            }
        };

        const handleDeleteEdu = async(educationno) =>{ //학력 삭제
            const url = `/COCO/api/teacher/deleteeducation.json?educationno=${educationno}`;
            const headers = {
                "Content-Type" : "application/json", "TOKEN" : state.token
            };
            if(confirm('삭제하시겠습니까?')){
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                handleData();
            }
        };

        return {state, handleDeleteEdu, handleBtn, handleInsert, handlePrev, expanded: ref(false), options: ['재학', '졸업', '중퇴']};
    }
}
</script>

<style lang="css" scoped src="@/assets/css/General.css">

</style>