<template>
    <div>
      <template v-for="(item, index) in items">
        <div class="divider" v-if="item.type === 'divider'" :key="`divider${index}`" />
        <menu-item v-else :key="index" v-bind="item" />
      </template>
      
      <!-- 이미지 다이얼로그 -->
      <q-dialog v-model="dialog" class="dialog">
        <q-card>
          <q-card-section>
            <div class="text-h6">Input File</div>
          </q-card-section>

          <q-card-section class="q-pt-none">
            <input type="file" ref="image" />
          </q-card-section>

          <q-card-actions align="right" class="text-primary">
            <q-btn flat label="Cancel" v-close-popup />
            <q-btn flat label="Add Image" @click="uploadImg" v-close-popup />
          </q-card-actions>
        </q-card>
      </q-dialog>

      <!-- 유튜브 다이얼로그 -->
      <q-dialog v-model="dialog2" class="dialog">
        <q-card>
          <q-card-section>
            <div class="text-h6">InputYoutubeUrl</div>
          </q-card-section>

          <q-card-section class="q-pt-none">
            <input type="text" id="url" ref="url" placeholder="Input YouTube Url" style="width:300px"/>
          </q-card-section>

          <q-card-actions align="right" class="text-primary">
            <q-btn flat label="Cancel" v-close-popup />
            <q-btn flat label="Add Youtube" @click="addVideo" v-close-popup />
          </q-card-actions>
        </q-card>
      </q-dialog>
    </div>
  </template>
  
  <script>
  import MenuItem from './MenuItem.vue'
  import axios from 'axios' 
import { ref } from '@vue/reactivity'
  
  export default {
    components: {
      MenuItem,
    },
  
    props: {
      editor: {
        type: Object,
        required: true,
      },
    },

    methods:{
      uploadImg() {
        this.input.image = this.$refs['image'].files[0]
        console.log(this.input.image);

        // const url = URL.createObjectURL(image)
        
        let form = new FormData()
        form.append('file',this.input.image)

        axios.post(`/COCO/api/testboard/insertimg.json`, form)
          .then( response => {
            console.log(response.data.result)
            this.editor.chain().focus().setImage({ src: response.data.result }).run()
            this.dialog = false
          }).catch( error => {
            console.log('failed', error)
          })
      },
      addVideo() {
        // const url = prompt('Enter YouTube URL')
        this.input.url = this.$refs['url'].value 

        this.editor.commands.setYoutubeVideo({
          src: this.input.url,
          width: Math.max(320, parseInt(this.width, 10)) || 640,
          height: Math.max(180, parseInt(this.height, 10)) || 480,
        })
        this.dialog2 = false
      },
    },
  
    data() {
      return {
        // dialog: false,
        // dialog2: false,
        input: {
          image : '',
          url   : '',
        },
        items: [
          {
            icon: 'bold',
            title: 'Bold',
            action: () => this.editor.chain().focus().toggleBold().run(),
            isActive: () => this.editor.isActive('bold'),
          },
          {
            icon: 'italic',
            title: 'Italic',
            action: () => this.editor.chain().focus().toggleItalic().run(),
            isActive: () => this.editor.isActive('italic'),
          },
          {
            icon: 'strikethrough',
            title: 'Strike',
            action: () => this.editor.chain().focus().toggleStrike().run(),
            isActive: () => this.editor.isActive('strike'),
          },
          {
            icon: 'code-view',
            title: 'Code',
            action: () => this.editor.chain().focus().toggleCode().run(),
            isActive: () => this.editor.isActive('code'),
          },
          {
            icon: 'mark-pen-line',
            title: 'Highlight',
            action: () => this.editor.chain().focus().toggleHighlight().run(),
            isActive: () => this.editor.isActive('highlight'),
          },
          {
            type: 'divider',
          },
          {
            icon: 'h-1',
            title: 'Heading 1',
            action: () => this.editor.chain().focus().toggleHeading({ level: 1 }).run(),
            isActive: () => this.editor.isActive('heading', { level: 1 }),
          },
          {
            icon: 'h-2',
            title: 'Heading 2',
            action: () => this.editor.chain().focus().toggleHeading({ level: 2 }).run(),
            isActive: () => this.editor.isActive('heading', { level: 2 }),
          },
          {
            icon: 'paragraph',
            title: 'Paragraph',
            action: () => this.editor.chain().focus().setParagraph().run(),
            isActive: () => this.editor.isActive('paragraph'),
          },
          {
            icon: 'list-unordered',
            title: 'Bullet List',
            action: () => this.editor.chain().focus().toggleBulletList().run(),
            isActive: () => this.editor.isActive('bulletList'),
          },
          {
            icon: 'list-ordered',
            title: 'Ordered List',
            action: () => this.editor.chain().focus().toggleOrderedList().run(),
            isActive: () => this.editor.isActive('orderedList'),
          },
          {
            icon: 'list-check-2',
            title: 'Task List',
            action: () => this.editor.chain().focus().toggleTaskList().run(),
            isActive: () => this.editor.isActive('taskList'),
          },
          {
            icon: 'code-box-line',
            title: 'Code Block',
            action: () => this.editor.chain().focus().toggleCodeBlock().run(),
            isActive: () => this.editor.isActive('codeBlock'),
          },
          {
            type: 'divider',
          },
          {
            icon: 'double-quotes-l',
            title: 'Blockquote',
            action: () => this.editor.chain().focus().toggleBlockquote().run(),
            isActive: () => this.editor.isActive('blockquote'),
          },
          {
            icon: 'separator',
            title: 'Horizontal Rule',
            action: () => this.editor.chain().focus().setHorizontalRule().run(),
          },
          {
            type: 'divider',
          },
          {
            icon: 'text-wrap',
            title: 'Hard Break',
            action: () => this.editor.chain().focus().setHardBreak().run(),
          },
          {
            icon: 'format-clear',
            title: 'Clear Format',
            action: () => this.editor.chain()
              .focus()
              .clearNodes()
              .unsetAllMarks()
              .run(),
          },
          {
            type: 'divider',
          },
          {
            icon: 'arrow-go-back-line',
            title: 'Undo',
            action: () => this.editor.chain().focus().undo().run(),
          },
          {
            icon: 'arrow-go-forward-line',
            title: 'Redo',
            action: () => this.editor.chain().focus().redo().run(),
          },
          {
            type: 'divider',
          },
          {
            icon: 'image-line',
            title: 'Image',
            action: () => this.dialog = true
          },
          {
            icon: 'youtube-fill',
            title: 'Youtube',
            action: () => this.dialog2 = true
          },
        ],
      }
    },
    setup () {
    return {
      dialog: ref(false),
      dialog2: ref(false),
    }
  }
  }
  </script>
  
  <style lang="scss">
  .divider {
    width: 2px;
    height: 1.25rem;
    background-color: rgba(#000, 0.1);
    margin-left: 0.5rem;
    margin-right: 0.75rem;
  }
  .dialog {
    width: 600px;
    height: 300px;
  }
  </style>