<template>
    <div class="row">

        <div class="col-1"></div>

        <div class="col-10">

            <br />
            <div v-if="state.imageList">
                <tbody>
                    <tr v-for="tmp of state.imageList" :key="tmp" style="display:inline-block; padding: 15px;">
                            <td hidden>{{tmp.teacherimageno}}</td>
                            <td><img :src="`/COCO/api/teacher/image.json?teacherno=${tmp.teacherimageno}`"
                                style="max-width: 200px; height: 150px; margin:15px" /></td>
                    </tr>
                </tbody>
            </div>

            <menu-1 v-if="(state.menu = 1)">
            
            </menu-1>

        </div>

        <div class="col-1"></div>

    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import { useRoute } from 'vue-router';
import { computed, onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';
import axios from 'axios';
import Menu1 from '@/components/cours/ManageCoursPage.vue';
export default {
    components : {
        Menu1
    },
    setup() {
        const route = useRoute();
        const store = useStore();
        const state = reactive({
            id : '',
            pw : '',
            tokenParam : route.query.token,
            AppToken : computed(() => store.getters.getAppToken),
            token : sessionStorage.getItem("TOKEN"),
            memberInfo : computed(() => store.getters.getMemberInfo),
            teacherInfo : computed(() => store.getters.getTeacherInfo),
            imageList : [],
            menu : 1
        })

        const handleToken = async() => {
            console.log('최초 접속시',state.tokenParam);
            if(state.tokenParam !== undefined) {
                sessionStorage.setItem("TOKEN", state.tokenParam);
                store.commit('setAppToken', state.tokenParam)
            }
            handleMain();
        }

        const handleMain = () => {
            store.commit('setAppToken', state.token);
            store.dispatch('handleMemberData');
            store.dispatch('handleTeacherData');
        }

        const handleImageData = async() => {
            const url = `/COCO/api/teacher/selectimage.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.imageList = data.image;
            }
        }

        onMounted(() => {
            handleToken();
            handleImageData();
        })

        return {state}
    }
}
</script>

<style lang="css" scoped src="@/assets/css/General.css">

</style>