<template>
    <div class="row">

        <div class="col-1"></div>

        <div class="col-10">
            <div class="flex flex-center text-bold text-subtitle1">강좌 관리</div>

            <br />
            <q-markup-table flat wrap-cells style="margin-top:12px;width:100%">
                <thead class="bg-secondary text-white">
                    <th class="text-left">강좌번호</th>
                    <th class="text-center">강좌명</th>
                    <th class="text-right">가격</th>
                    <th class="text-right">난이도</th>
                    <th class="text-right">상태</th>
                    <th class="text-right">등록일</th>
                </thead>
                <tbody>
                    <tr v-for="tmp in state.rows" :key="tmp" @click="handleSelectOne(tmp.coursno)" class="cours-row">
                        <td class="text-left" width="15%">{{tmp.coursno}}</td>
                        <td class="text-left">{{tmp.title}}</td>
                        <td class="text-right" width="10%">{{tmp.price}}</td>
                        <td class="text-right" width="12%">{{tmp.difficult}}</td>
                        <td class="text-right" width="10%">{{tmp.status}}</td>
                        <td class="text-right" width="13%">{{moment(tmp.regdate).format('yyyy MM-DD')}}</td>
                    </tr>
                </tbody>
            </q-markup-table>
        </div>

        <div class="col-1"></div>

    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
export default {
    setup() {
        const moment = require('moment')
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem('TOKEN'),
            rows : [],
            coursno : 0
        })

        const handleSelectOne = async(obj) => {
            console.log(obj)
            router.push({path:'/coursselectone', query : {coursno : obj}})
        }

        const handleData = async() => {
            const url = `/COCO/api/solda/selectcours.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }

            const { data } = await axios.get(url, {headers});
            console.log(data.cours);
            if(data.status === 200) {
                state.rows = data.cours;
            }
        }

        onMounted(() => {
            handleData();
        })

        return {
            state,
            moment,
            handleSelectOne
        }
    }
}
</script>

<style lang="css" scoped>
    .cours-row:hover {
        cursor: pointer;
    }
</style>