<template>
    <div>

        <q-card style="min-width:80%; height:80%; padding:20px;">
    
            <q-btn @click="handleBack" icon="chevron_left" square color="grey-7" class="fab" flat />
    
            <q-card-section>
                <div class="text-h6">{{state.lectureData.title}}</div>
                <!-- <q-space />
                <q-btn dense color="negative" label="닫기" v-close-popup /> -->
            </q-card-section>
    
            <q-card-section >
                <!-- <video style="width:100%" controls>
                    <source :src="`/COCO/api/lecture/video?no=${state.videoNo}`" type="video/mp4" />
                    
                </video> -->
    
            <!-- <q-card style="min-width:800px;min-height:600px;"> -->
    
                <q-video v-if="state.videoNo !== 0" style="width:100%;height:50vh;" :src="`/COCO/api/lecture/video?no=${state.videoNo}`"/>
    
                <div v-if="state.videoNo === 0" style="width:100%;max-width:100%;">
                    <div class="text-grey-7">해당 강의는 동영상이 없습니다.</div>
                </div>
    
            <!-- </q-card> -->
            </q-card-section>
    
    
            <q-card-section style="width:100%;" class="scroll">
                <div id="board"></div>
            </q-card-section>
    
            <!-- <q-separator />
    
            <q-card-actions align="right">
                <q-btn dense color="negative" label="닫기" v-close-popup />
            </q-card-actions>
            -->
            </q-card>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const router = useRouter();
        const route = useRoute();
        const state = reactive({
            lectureNo : route.query.no,
            lectureData : [],
            videoNo : 0,
        })

        const handleSelectOneLecture = async() =>{
            const url = `/COCO/api/lecture/selectone.json?lectureno=${state.lectureNo}`
            const headers = {
                "Content-Type":"application/json"
            }
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                state.lectureData = data.lecture;
                state.videoNo = data.videono;
                state.lectureNo = data.lecture.no
                document.getElementById("board").innerHTML = data.lecture.content
            }
        }

        const handleBack = () => {
            router.go(-1);
        }

        onMounted(() => {
            handleSelectOneLecture();
        })

        return {
            state,
            handleBack
        }
    }
}
</script>

