<template>
    <div class="container">
        <q-card-section>
            <div class="text-h6 text-secondary">외부링크</div>
        </q-card-section>

        <div v-if="!state.linkList.length > 0" style="padding:15px" >
            <div class="text-caption text-grey">등록된 외부링크가 없습니다.</div>
            <div class="text-caption text-grey">최대 3개까지 추가할 수 있습니다.</div>
        </div>    
        <div>
                <div>
                    <q-card flat>
                        <q-item>
                            <q-item-section class="vfor-box">
                                <div v-for="tmp of state.linkList" :key="tmp" class="row" style="padding: 15px;">
                                    <q-card class="row">
    
                                        <q-card-section class="row" style="width:450px; ">
                                            <div hidden>{{tmp.linkno}}</div>
    
                                            <div class="link-box" @click="handleHref(tmp.platform, tmp.url)">
                                                <img class="link-img" :src="handleIcon(tmp.platform)" />
                                            </div>
    
                                            <div>{{tmp.platform}}</div>
                                            <div>.com/</div>
                                            <div style="width:160px;">{{tmp.url}}</div>
                                        </q-card-section>
    
                                        <q-separator vertical />
    
                                        <q-card-actions>
                                            <div><q-btn color="negative" flat dense @click="handleDelete(tmp.linkno)" label="삭제" /></div>
                                        </q-card-actions>
    
                                    </q-card> 
                                </div>
                            </q-item-section>
                        </q-item>
                    </q-card>
                </div>
    
                <q-card-actions>
                    <q-btn color="grey" flat dense label="외부링크 추가" :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'" @click="expanded = !expanded" />
                </q-card-actions>
            </div>

            <q-slide-transition>
                <div v-show="expanded">
                    <div class="q-pa-md row">
                        <q-select style="width:90px;" color="secondary" class="input-item" options-dense dense v-model="state.platform"
                        option-value="value" option-label="label" emit-value map-options :options="options" label="플랫폼" />
                        <q-input style="width:160px;" color="secondary" dense v-model="state.link.url" class="input-item" autofocus label="주소" />
                        <q-btn flat dense color="primary" label="추가" @click="handleInsert" />
                    </div>
                </div>
            </q-slide-transition>
        </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity';
import { onMounted } from 'vue';
import axios from 'axios';
export default {
    setup() {
        const state = reactive({
            linkBtn : true,
            linkB : false,
            link : [],
            linkList : [],
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData();
        });

        // 버튼 추가
        const handleBtn = () => {
            state.linkBtn = false;
            state.linkB = true;
        };

        // 버튼 취소
        const handlePrev = () => {
            state.linkBtn = true;
            state.linkB = false;
        };

        // 링크 전체 조회
        const handleData = async() => {
            const url = `/COCO/api/teacher/selectListlink.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.linkList = data.link;
            }
        };

        // 링크 등록
        const handleInsert = async() => {
            const url = `/COCO/api/teacher/joinlink.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                platform : state.link.platform,
                url : state.link.url,
            }
            
            const { data } = await axios.post(url, body, {headers});
            console.log(data)
            if(data.status === 200) {
                state.linkB = false;
                state.linkBtn = true
                handleData();
            }
        };
        
        // 링크 삭제
        const handleDelete = async(linkno) => {
            const url = `/COCO/api/teacher/deletelink.json?linkno=${linkno}`;
            const headers = {
                "Content-Type" : "application/json", "TOKEN" : state.token
            };
            if(confirm('삭제하시겠습니까?')){
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                handleData();
            }
        };
        
        const handleHref = (platform, url) => {
                window.open(`https://${platform}.com/${url}`)
        };

        const handleIcon = (obj) => {
            let src = require(`@/assets/svg/icon/${obj}.svg`)
            return src
        };

        return {state, handleInsert, handleDelete, handleBtn, handlePrev, expanded: ref(false),
            platform: ref(null), handleHref, handleIcon,
            options : ['youtube','twitch','instagram','facebook']};
        }
    }
</script>

<style lang="css" scoped src="@/assets/css/General.css">

</style>