<template>
    <div class="container" >
    <div class="q-pa-md row items-start q-gutter-md">
        <q-card class="my-card" flat>
            <q-card-section>
            <div class="text-h6 text-secondary">강사소개</div>
        </q-card-section>

        <div class="q-pa-md" style="margin-top:-20px;">
            <q-card>
                <q-input borderless autogrow type="textarea" color="secondary" v-model="state.intro" 
                style="width: 700px; height: 340px;"  placeholder="자신을 소개하는 내용을 써 주세요." counter maxlength="1000" />
            </q-card>
            
            <div style="margin-top:20px;">
                <q-btn class="btn-frame" dense color="primary" label="수정" @click="handleUpdate" />
            </div>
        </div>
        </q-card>

    </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
// import { useRoute } from 'vue-router';
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
export default {
    setup () {
        // const route = useRoute();
        const state = reactive({
            teacherno : '',
            cate : '',
            intro : '',
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData(); // 강사 자동 조회
        });
        
        // 강사 조회
        const handleData = async () => {
            const url = `/COCO/api/teacher/selectOneteacher.json`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.teacherno = data.result.teacherno,
                state.cate = data.result.category,
                state.intro = data.result.intro
            }
        };
        
        // 강사 수정
        const handleUpdate = async () => {
            const url = `/COCO/api/teacher/updateteacher.json`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const body = {intro : state.intro};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('수정완료');
            }
        };
        
        return {state, handleUpdate};
    }
}
</script>

<style lang="css" scoped src="@/assets/css/General.css">

</style>