import { createWebHistory, createRouter } from "vue-router";

import MainPage from '@/components/MainPage.vue';
import LoginPage from '@/components/LoginPage.vue';

import EducationPage from '@/components/teacher/EducationPage.vue';
import BasicPage from '@/components/teacher/BasicPage.vue';
import SocialPage from '@/components/teacher/SocialPage.vue';
import TimagePage from '@/components/teacher/TimagePage.vue';

import InsertCoursPage from '@/components/cours/InsertCoursPage.vue';
import ManageCoursPage from '@/components/cours/ManageCoursPage.vue';
import CoursSelectOnePage from '@/components/cours/CoursSelectOnePage.vue';

import InsertLecturePage from '@/components/lecture/InsertLecturePage.vue';
import LectureSelectOnePage from '@/components/lecture/LectureSelectOnePage.vue';

const routes = [
    {path:'/', redirect:'/main'},
    {path:'/main', name:'MainPage', component:MainPage},
    {path:'/login', name:'LoginPage', component:LoginPage},

    {path:'/education', name:'EducationPage', component:EducationPage},
    {path:'/basic', name:'BasicPage', component:BasicPage},
    {path:'/social', name:'SocialPage', component:SocialPage},
    {path:'/timage', name:'TimagePage', component:TimagePage},

    {path:'/insertcours', name:'InsertCoursPage', component:InsertCoursPage},
    {path:'/managecours', name:'ManageCoursPage', component:ManageCoursPage},
    {path:'/coursselectone', name:'CoursSelectOnePage', component:CoursSelectOnePage},
    
    {path:'/insertlecture', name:'InsertLecturePage', component:InsertLecturePage},
    {path:'/selectonelecture', name:'LectureSelectOnePage', component:LectureSelectOnePage},
]

// 라우트 객체 생성
const router = createRouter({
    history : createWebHistory('/COCO'),
    routes  : routes,
});

export default router;