
<template>
  <div class="q-pa-md">
    <q-layout view="lHh Lpr lff" container style="height: 100vh" class="shadow-2 rounded-borders">
  
      <q-header class="bg-secondary">
        <q-toolbar>
          <q-btn flat @click="drawer = !drawer" round dense icon="list" />

            <q-toolbar-title>
              강사페이지
            </q-toolbar-title>

            <q-space />

            <div class="text-subtitle1 site-logo" @click="handleStudentVue()">
              <img src="@/assets/images/logo/logo_w.png" style="width:120px;height:24px;" />
            </div>

        </q-toolbar>
      </q-header>

      <q-drawer v-model="drawer" elevated class="text-secondary" show-if-above :width="200" :breakpoint="900">
        <q-scroll-area class="fit">
          <q-list padding bordered class="menu-list">

            <q-item style="height:100px">
              <div class="q-pa-sm absolute-top">

                <div class="avatar row">
                    <div v-if="state.memberInfo.img != null" class="avatar-box" >
                      <q-avatar class="avatar-radius z-max" size="50px" color="white">
                        <q-avatar class="avatar-member" size="50px" >
                          <img :src="`/COCO/api/member/image?no=${state.memberInfo.img}`"/>
                        </q-avatar>
                      </q-avatar>
                    </div>
                    
                    <div v-if="state.memberInfo.img == null" class="avatar-box">
                      <q-avatar class="avatar-radius" size="50px" color="white">
                        <q-avatar class="avatar-member" size="50px">
                          <img :src="state.avatar" />
                        </q-avatar>
                      </q-avatar>
                    </div>        

                    <q-item-section class="q-pl-md text-black text-subtitle1">{{state.memberInfo.id}}</q-item-section>

                </div>


              </div>  
            </q-item>
            
      <q-separator />

      <q-expansion-item  header-class="text-secondary" label="HOME" to="/main" icon="home"
                default-closed hide-expand-icon >
              
      </q-expansion-item>

            <!-- <q-item to="/main" exact>

              <q-item-section  class="row">
                Home
              </q-item-section>
            </q-item> -->

            <q-expansion-item label="정보변경" icon="settings"
                default-closed  hide-expand-icon >

              <q-item to="/basic" dense class="text-grey text-center" exact>

                <q-item-section>
                  기본정보
                </q-item-section>
              </q-item>

              <q-item to="/education" dense class="text-grey text-center" exact>

                <q-item-section>
                  학력정보용
                </q-item-section>
              </q-item>

              <q-item to="/social" dense class="text-grey text-center" exact>

                <q-item-section>
                  외부링크
                </q-item-section>
              </q-item>

              <q-item to="/timage" dense class="text-grey text-center" exact>

                <q-item-section>
                  이미지
                </q-item-section>
              </q-item>

            </q-expansion-item>

            <q-expansion-item label="강좌" icon="lyrics"
                default-closed  hide-expand-icon >


    
              <q-item to="/insertcours" dense class="text-grey text-center" exact>

                <q-item-section>
                  강좌생성
                </q-item-section>
              </q-item>

              <q-item to="/managecours" dense class="text-grey text-center" exact>

                <q-item-section>
                  강좌관리
                </q-item-section>
              </q-item>

            </q-expansion-item>

          </q-list>
        </q-scroll-area>
      </q-drawer>

      <q-page-container>
        <q-page padding>
          <router-view></router-view>
        </q-page>
      </q-page-container>
    </q-layout>

  </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import { computed, onMounted } from '@vue/runtime-core'
import { useStore } from 'vuex';


export default {
  setup () {
    const store = useStore();
    const state = reactive({
      token : sessionStorage.getItem("TOKEN"),
      appToken : computed(() => store.getters.getAppToken),
      memberInfo : computed(() => store.getters.getMemberInfo),
      avatar : require('@/assets/images/logo/coconut_500.png'),
      teacherInfo : computed(() => store.getters.getTeacherInfo)
    })

    const handleStudentVue = () => {
      //빌드전
      // window.location.href = ("http://localhost:9090?token="+state.token);
      //빌드후
      // window.location.href = ("http://127.0.0.1:8081/COCO/");
      //배포후
      window.location.href = ("http://3.35.91.60:8080/COCO/");
    };
        
    const drawer = ref(false)

    onMounted(() => {
      store.commit('setAppToken', state.token);
      store.dispatch('handleMemberData');
      store.dispatch('handleTeacherData');
    })

    return {
      state,
      handleStudentVue,
      drawer,
      toggleDrawer () {
        drawer.value = !drawer.value
      }
    }
  }
}
</script>

<style lang="css" scoped>
  @import url('@/assets/css/App.css');
  .site-logo:hover {
    cursor: pointer;
  }
</style>