<template>
    <div>
        <h4>로그인 페이지입니다</h4>
        <input type="text" placeholder="아이디" v-model="state.id" />
        <input type="password" placeholder="비밀번호" v-model="state.pw" />
        <input type="text" v-model="state.id" />
        <input type="password" v-model="state.pw" />
        <button @click="handleTeacherJoin()">등록</button>

        <button @click="handleData()">토큰 확인</button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import axios from 'axios';
import { useRoute } from 'vue-router';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const route = useRoute();
        const state = reactive({
            id : '',
            pw : '',
            tokenParam : route.query.token
        })


        const handleData = async() => {
            const url = `/COCO/api/member/selectone.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.tokenParam
            }
            const { data } = await axios.get(url, {headers});
            console.log(data)
        }

        const handleToken = async() => {
            console.log(state.tokenParam);
            sessionStorage.setItem("TOKEN", state.tokenParam);
        }

        onMounted(() => {
            handleToken();
        })

        return {state, handleData}
    }
}
</script>

