import axios from "axios";
import { createStore } from "vuex";

export default createStore({
    state() {

        let tmp = false;
        if(sessionStorage.getItem("TOKEN") !== null){
            tmp = true;
        }
        return {
            logged : tmp,
            appToken : sessionStorage.getItem("TOKEN"),
            memberInfo : [],
            teacherInfo : []
        }
    },

    getters : {
        getLogged(state) {
            return state.logged;
        },
        getAppToken(state) {
            return state.appToken;
        },
        getMemberInfo(state) {
            return state.memberInfo;
        },
        getTeacherInfo(state) {return state.teacherInfo},
    },

    mutations : {
        setLogged(state, value) {
            state.logged = value;
        },
        setAppToken(state, value) {
            state.appToken = value;
        },
        setMemberInfo(state, value) {
            state.memberInfo = value;
        },
        setTeacherInfo(state, value) {state.teacherInfo = value},
    },

    actions : {
        async handleMemberData(context) {
            const url = `/COCO/api/member/selectone.json`;
            const headers = {
            "Content-Type":"application/json",
            "TOKEN" : sessionStorage.getItem("TOKEN")
        };
        if(sessionStorage.getItem("TOKEN") !== null) {
            const { data } = await axios.get(url, {headers});
            console.log('회원조회',data);
            context.commit('setMemberInfo', data.result)
            }
        },

        async handleTeacherData(context) {
            const url = `/COCO/api/teacher/selectOneteacher.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : sessionStorage.getItem("TOKEN")
            };
            if(sessionStorage.getItem("TOKEN") !== null) {
                const { data } = await axios.get(url, {headers});
                console.log('강사조회',data);
                if(data.status === 200) {
                    context.commit('setTeacherInfo', data.result);
                }
            }
        },
    }
});