<template>
    <div class="q-pa-md row">
        <div class="col-1"></div>
        <div class="col-10">
            <q-card flat>
                <div class="row" style="height : 300px;">
                    
                    <div class="col-7">
                        <q-card>
                            <q-input class="q-pa-md" color="secondary" v-model="state.title" square label="강좌명" />

                            <div class="q-pa-md">
                                <div class="text-grey-7" style="padding:10px;">태그는 최대 5개까지 가능합니다.</div>
                                <div>
                                    <q-checkbox v-model="state.genre" val="Blues" label="Blues" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Jazz" label="Jazz" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Rock" label="Rock" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="R&B" label="R&B" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Funk" label="Funk" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Hip-Hop" label="Hip-Hop" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Pop" label="Pop" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="Classic" label="Classic" color="secondary" />
                                    <q-checkbox v-model="state.genre" val="EDM" label="EDM" color="secondary" />
                                
                                </div>

                                <div>
                                    <q-checkbox v-model="state.genre" val="음악이론" label="음악이론" color="accent" />
                                    <q-checkbox v-model="state.genre" val="개별곡" label="개별곡" color="accent" />
                                    <q-checkbox v-model="state.genre" val="컴퓨터음악" label="컴퓨터음악" color="accent" />
                                </div>
    
                                <div class="q-pa-md row">
                                    <q-select color="secondary" style="max-width:95px; min-width: 60px;" options-dense dense borderless
                                        v-model="state.difficult" :options="difficult" label="난이도" />

                                    <q-select color="secondary" style="max-width:135px;min-width: 70px;"  options-dense dense borderless
                                        v-model="state.inst" :options="inst" label="카테고리" />

                                    <q-input style="width:68px; margin-left: 30px;" color="secondary" dense label="가격" v-model.number="state.price" 
                                        reverse-fill-mask input-class="text-right" mask="#" suffix="원" />
                                </div>
                            </div>
                        </q-card>
                    </div>

                    <div class="col-1"></div>

                    <div class="col-4 row">
                        <q-card flat>
                            <div class="file-box">
                                <label for="input-file">                                
                                    메인이미지 첨부
                                </label>
                                <input type="file" id="input-file" @change="handleImage($event)" style="display:none" />
                            </div>
                            <br />
                            <div style="">
                                <img class="main-img" :src="state.img" />
                            </div>
                        </q-card>
                    </div>


                </div>

            </q-card>

            <editor style="margin-top:60px;" class="editor" v-model="state.intro" />

            <q-card align="right" flat style="margin:20px;">
                <q-btn side class="btn-frame" dense color="primary" label="강좌 생성" @click="handleInsertCours" />
            </q-card>            
        </div>
        <div class="col-1"></div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router'
import Editor from '@/components/editor/GeneralEditor.vue'
export default {
    components : {
        Editor
    },
    data() {
        return {
            content : this.state.intro
        }
    },
    setup() {
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            title : '',
            genre : [], // 줄 때 name으로 
            img : require('@/assets/images/no_image.png'),
            file : '',
            intro : '강좌소개',
            price : 0,
            difficult : '초급',
            inst : '보컬(남)',
        });

        const handleInsertCours = async() => {
            if(state.title === '') {
                alert('강좌명을 입력하세요.')
                return false;
            }
            if(state.price < 5000) {
                alert('최소 가격은 5000원입니다.')
                return false;
            }
            if(state.img === require('@/assets/images/no_image.png')) {
                alert('이미지를 첨부해야 합니다.')
                return false;
            }
            if(state.genre.length > 5) {
                alert('태그는 최대 5개까지 가능합니다.')
                return false;
            }
            

            const url = `/COCO/api/cours/make.json`;
            const headers = {
                "Content-Type" : "multipart/form-data",
                "TOKEN" : state.token
            }
            const body = new FormData();
            body.append("title", state.title);
            for(let i=0; i<state.genre.length; i++) {
                body.append("name", state.genre[i]);
            }
            body.append("difficult", state.difficult);
            body.append("intro", state.intro);
            body.append("price", state.price);
            body.append("period", '무제한');
            body.append("status", '편집');
            body.append("file", state.file);
            body.append("instrument", state.inst);
            
            const { data } = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200) {
                alert('강좌가 생성되었습니다.');
                router.push({path:'/managecours'})
            }
        }

        const handleImage = (e) => {
            console.log(e);
            if(e.target.files.length > 0) { // 파일을 선택함
                state.file = e.target.files[0];
                state.img  = URL.createObjectURL(e.target.files[0]);
            }
            else { // 파일을 취소함
                state.file = '';
                state.img = require('@/assets/images/no_image.png');
            }
        };

        return {
            state,
            handleInsertCours,
            handleImage,
            difficult : ['입문', '초급', '중급', '전문가'],
            inst : ['보컬(남)','보컬(여)','일렉트릭 기타','베이스 기타','어쿠스틱 기타', '신디사이저', '피아노','드럼', 'MIDI', 'ETC']
        }
    }
}
</script>

<style lang="css" scoped>
.file-box label {
    width : 120px;
    height: 24px;
    color : white;
    font-weight: 600;
    font-size : 16px;
    cursor: pointer;
    border : 1px solid white;
    border-radius: .25em;
    padding: .1em .75em;
    margin : 2.5px;
    right : 100px;
    position: relative;
    left : -3px;
    top: -5;
    text-align: center;
    background : #4d9056;
    opacity: 1;
}
.file-box label:hover {
    /* opacity: 0.5; */
    background-color: rgba(77,144,86, 0.8);
    /* color : white; */
}
.main-img {
    width: 100%;
    /* height: 200px; */
    max-height: 300px;
    top:0px;
    position: relative;
}
</style>